<template>
  <div class="row">
    <div class="col-12 mt-3" v-if="isReady">
      <div class="row">
        <div class="col-12 flex-x-between mb-3">
          <div class="flex-align-center">
            <el-button
              v-ripple.400
              type="dark"
              class="no-width flat"
              @click="$router.go(-1)"
            >
              <i class="bx bx-left-arrow-alt"></i>
            </el-button>
            <h5 class="my-0 ml-2">Membership plans</h5>
          </div>
        </div>
        <div class="col-lg-12 route-content-view mb-3">
          <div class="row">
            <div class="col-4 px-3" v-for="plan in plans" :key="plan.id">
              <div class="plan-box p-3">
                <div class="plan-box-header">
                  <div>
                    <h3 class="mb-3 mt-5 text-center">{{ plan.name }}</h3>
                    <p class="gray mb-5 text-center">
                      {{ plan.description }}
                    </p>
                  </div>
                  <div class="flex-column flex-align-center">
                    <h1 class="plan-price mb-3">
                      {{ plan.price
                      }}<span class="gray normal-text">MMK/mo</span>
                    </h1>
                    <el-button
                      type="primary"
                      @click="
                        $router.push({
                          name: 'Add Billing',
                          query: {
                            pid: plan.id,
                          },
                        })
                      "
                      class="mx-0 mb-3"
                      >Choose plan</el-button
                    >
                  </div>
                </div>
                <div class="b-divider"></div>
                <p class="font-weight-bold">FEATURES</p>
                <p
                  v-for="feature in JSON.parse(plan.features)"
                  :key="feature.name"
                >
                  {{ feature }}
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from "vuex";
export default {
  data() {
    return {
      plans: [],
    };
  },
  watch: {},
  mounted() {
    // if (!this.isStoreExpired) {
    //   this.$router.push({ name: "Plan" });
    // }
    this.$store.commit("setIsReady", false);
    this.getMemberships();
  },
  methods: {
    getMemberships() {
      this.$axios
        .get("memberships/")
        .then((response) => {
          this.plans = response.data;
          this.$store.commit("setIsReady", true);
        })
        .catch((err) => {
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
  },
  computed: {
    ...mapState(["store", "isReady"]),
    ...mapGetters(["isStoreExpired"]),
  },
};
</script>

<style >
.plan-box-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  min-height: 38vh;
}
</style>